
import { defineComponent } from "vue";
import { Constants } from "@/constants";

export default defineComponent({
  name: "SideBar",
  data() {
    return {
      menuList: Constants.MENU_ITEM_LIST,
      isMenuExpand: true,
    };
  },
  methods: {
    toggleSidebar() {
      this.isMenuExpand = !this.isMenuExpand;
    },
  },
});
